import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Video from '@/components/Video'
import Sbt from '@/components/home/Sbt'
import Tj from '@/components/home/Tj'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      children:[
        {path: '', redirect: 'Sbt'},//如果子路由是空的 跳转到 des
        {path:"Sbt",
          component:Sbt
       },
        {path:"Tj",
          component:Tj
        }
      ]
    },
      {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld,
      children:[
        {path: '', redirect: 'Sbt'},//如果子路由是空的 跳转到 des
        {path:"Sbt",
          component:Sbt
        }
        ,
        {path:"Tj",
          component:Tj
        }
        ]
      
    },
    {
      path: '/Video',
      name: 'Video',
      component: Video
    }
  ]
})



